import React, { useEffect, useRef, useState } from 'react'
import LazyImage from '../../Components/UI/LazyImage'
import { Link } from 'react-router-dom'

const AdsSwiper = ({ads}) => {
    const [height, setHeight]=useState(0)
    useEffect(()=>{
        setHeight(0)
        const handleResize = ()=>{
            setHeight(containerRef.current.getBoundingClientRect().width /3)
        }
        handleResize()
        window.addEventListener('resize', handleResize);

        ////////////////////////////////////////////////////
        //
        let timer
        let times = 0 
        if(ads){ 
            timer = setInterval(()=>{
                containerRef.current.scrollTo({
                    left: times * (containerRef.current.scrollWidth / ads.length), // Change this value to the desired scroll position
                    behavior: 'smooth' // This makes it scroll smoothly
                }); 
                times += 1
                if (times == ads.length ) times = 0

        }, 3000)}

        return () => {
            window.removeEventListener('resize', handleResize);
            clearInterval(timer) 
            times = 0
        }
    }, [ads])
    const containerRef = useRef()
    
  return (
    <div className='mx-2 mw-700 overflow-x-auto px-0 d-flex my-2 flex-grow-1 rounded-3 bg-color-white' style={{height : height}}  ref={containerRef}>
       { 
            ads.map(ad=>{
                return (
                <Link target='_blank' to={ad.url} key={ad.order}className='col-12 overflow-hidden cursor-pointer' >
                    <LazyImage height='100%' width='100%' src={ad.image} />    
                </Link>)
            })
        }
    </div>
  )
}

export default AdsSwiper