import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import LazyImage from '../../Components/UI/LazyImage'

const StaticAd = ({ad}) => {
    const [refreshComponent, setRefreshComponent] = useState(false)
    useEffect(()=>{
        const handleResize = ()=>{
            setRefreshComponent(prev=>!prev)
        } 
        handleResize()
        window.addEventListener('resize', handleResize)

        return () => {
            window.removeEventListener('resize', handleResize);
        }
    }, [])
  return (
    window.innerWidth >= 966 && 
    <Link target='_blank' to={ad && ad.url} className='m-2 bg-color-white rounded-3' style={{width: 233, height: 233}}>
        { ad && <LazyImage src={ad.image} height='100%' className='aspect-ratio-1 rounded-3' />}
    </Link>
  )
}

export default StaticAd