import React, { useContext } from 'react'
import { TranslationContext } from './store/TranslationContext'
import Content from './Content'
import { BrowserContext } from './store/BrowserContext'

const TranslatedContent = () => {
    
    const {dictionary} = useContext(BrowserContext).browserData 
    const translate=(text)=>{
      return dictionary[text] || text
    }
  return (
    <TranslationContext.Provider value={{translate}}><Content/></TranslationContext.Provider>
  )
}

export default TranslatedContent