import './App.css'
import './bootstrap.min.css';
import React, { lazy, useEffect, useRef, useState } from 'react';
import { BrowserContext } from './store/BrowserContext';
import { UserContext } from './store/UserContext';
import axios from 'axios'
import SplashScreen from './Screens/SplashScreen';
import { getCookie, isMobileDevice } from './utils';
import TranslatedContent from './TranslatedContent';

const User = lazy(()=>import('./Static/User'))

let userDataFromBrowser = JSON.parse(localStorage.getItem('userData'))
const root = document.documentElement;


function App() {
  ////////////////////////////
  //
  const config = {
    headers : {
      "Content-Type": "multipart/form-data",
      'X-CSRFToken' : getCookie('csrftoken')
    }
  }
  /////////////////////////////
  //
  isMobileDevice() ? document.querySelector('html').classList.remove('is-not-mobile')  : document.querySelector('html').classList.add('is-not-mobile') 
  //////////////////////////////////////////////
  // GET BROWSER DATA AT FIRST LOAD
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const browserDataFromLocaleStorage = JSON.parse(localStorage.getItem('browserData'))
  const [browserData, setBrowserData] = useState(browserDataFromLocaleStorage ? browserDataFromLocaleStorage : null)
  useEffect(()=>{
    if (browserData === null ){
      setLoading(true)
      axios.get('/api/parameters/').then((response)=>{
        const browserDataFromServer = {...response.data, isDarkMode : false}
        setBrowserData(browserDataFromServer)
        localStorage.setItem('browserData', JSON.stringify(browserDataFromServer))
        setLoading(false)

      }).catch((error)=>{
        setError(error.response.data.detail || error.message)
        setLoading(false)
      })
    }else{
      setLoading(false)
    }
  }, [])
  /////////////////////////////////////////////
  // LANGUAGE HANLING
  const toggleLanguage = (newLanguage) => {
    setLoading(true)
    
    axios.post(
      '/api/set-language/',
      {language : newLanguage},
      config
      )
    .then(response => {
      const newBrowserData = {...response.data, isDarkMode : browserData.isDarkMode}
      setBrowserData(newBrowserData)
      localStorage.setItem('browserData', JSON.stringify(newBrowserData))
      setLoading(false)
    })
    .catch(error => {
      console.error('Error:', error)
      setLoading(false)
    });
  };
  // END LANGUAGE HANLING
  /////////////////////////////////////////////

  ////////////////////////////////////////////
  // USER DATA HANDLING
  const [userData, setUserData] = useState(userDataFromBrowser ? userDataFromBrowser : {})
  const refreshToken = ()=>{
    axios.post(
      '/api/user/refresh-user-token/',
      {refresh: userData.refresh, token : userData.token},
      {
        headers : {
          "Content-Type": "multipart/form-data",
        }
      }
    ).then(response=>{
      const newUserData = {
        ...userData,
        access : response.data.access_token,
        token : response.data.access_token,
        loginDate:new Date()

      }
      localStorage.setItem('userData', JSON.stringify(newUserData))
      setUserData(newUserData)
      setLoading(false)
      console.log('Changed')
    }).catch(error=>setLoading(false))
  }
  const loginDate = useRef(userData.loginDate)
  useEffect(()=>{
    loginDate.current = userData.loginDate
    /////////////////////
    //
    window.userToken = userData.token
  }, [userData])

  function checkTokenExpiry() {
    const cond = ((Date.now()-new Date(loginDate.current))/1000) > 2505600 // 29 days
    if (cond) {
      setLoading(true)
      refreshToken()
    }
  }  
  useEffect(()=>{
    let interval
    if(userData.id){
      checkTokenExpiry()
      setInterval(checkTokenExpiry, 3600000); // 3600000 milliseconds = 1 hour
    }
    return()=> clearInterval(interval) 
  },[])

  // END USER HANDLING
  ///////////////////////////////////////////

  //////////////////////////////
  //
  const [globalMessage, setGlobalMessage] = useState(null)
  const [searchCategories, setSearchCategories] = useState(['vehicles', 'users'])

  ////////////////////////////////////
  //
  useEffect(() => {
    const handleClick = (event) => {
      document.dispatchEvent(new CustomEvent('global-click', { detail: { target: event.target } }));
    };
    document.addEventListener('mousedown', handleClick);
    return () => {
        document.removeEventListener('mousedown', handleClick);
    };
}, []);
  
  ///////////////////////////////
  //
  const transformedParameters = {
    states : browserData && browserData.states.map(state=>{return {label : state.name, value : state.code}}),
    colors :browserData && browserData.colors.map(color=>{return {label : color.name, value : color.id}}),
    documents: browserData && browserData.documents.map(document=>{return {label : document.name, value : document.id}}),
    options: browserData && browserData.options.map(option=>{return {label : option.name, value : option.id}}),
    fuels: browserData && browserData.fuels.map(fuel=>{return {label : fuel.name, value : fuel.id}}),
    gearBoxs: browserData && browserData.gear_boxs.map(gear=>{return {label : gear.name, value : gear.id}}),
  }
  //////////////////////////
  //
  const [popupVisibility, setPopupVisibility] = useState({
    notificationsShown : false,
    conversationsShown : false,
    showCategories:false,
    sideBarOpen: false,
    filtrationSidebarShown: false,
    searchSuggestionsShown : false,
   })

  const togglePopup = (popupName) => {
    document.querySelectorAll('.popup').forEach(elem=>elem.setAttribute('hidden', ''))
    setPopupVisibility(prevVisibility => {
      const newVisibility = {...Object.fromEntries(Object.entries(prevVisibility).map(([name, value]) => [name, false]))}
      if(popupName) newVisibility[popupName] = ! prevVisibility[popupName]
      return newVisibility
    });
  };

  const openPopup = (popupName)=>{
    document.querySelectorAll('.popup').forEach(elem=>elem.setAttribute('hidden', ''))
    setPopupVisibility(prevVisibility => {
      const newVisibility = {...Object.fromEntries(Object.entries(prevVisibility).map(([name, value]) => [name, false]))}
      if(popupName) newVisibility[popupName] = true
      return newVisibility
    });
  }
  const closePopup = (popupName)=>{
    document.querySelectorAll('.popup').forEach(elem=>elem.setAttribute('hidden', ''))
    setPopupVisibility(prevVisibility => {
      const newVisibility = {...Object.fromEntries(Object.entries(prevVisibility).map(([name, value]) => [name, false]))}
      if(popupName) newVisibility[popupName] = false
      return newVisibility
    });
  }

  /////////////////////////
  //
  //////////////////////////////
  // SIDE BAR HANDLING
  if (popupVisibility['sideBarOpen'] || popupVisibility['filtrationSidebarShown'] ){
    document.getElementsByTagName('html')[0] && (document.getElementsByTagName('html')[0].style.overflow ='hidden')
    document.getElementById('root').classList.add('blur')
  }else{
    document.getElementsByTagName('html')[0] && (document.getElementsByTagName('html')[0].style.removeProperty('overflow'))
    document.getElementById('root').classList.remove('blur')
  } 

  /////////////////////////
  //
  const openReportModal = useRef()
  ///////////////////////
  //
  const Left = browserData && browserData.language == 'ar' ? 'right' : 'left'
  const Right = browserData && browserData.language == 'ar' ? 'left' : 'right'
  const S = browserData && browserData.language == 'ar' ? 'e' : 's'
  const E = browserData && browserData.language == 'ar' ? 's' : 'e'

  ///////////////////////
  //
 useEffect(()=>{
    if (browserData && browserData.isDarkMode){
      root.style.setProperty('--white', '#292929')
      root.style.setProperty('--white-rgb', '41, 41, 41')
      root.style.setProperty('--dark-white', '#000')
      root.style.setProperty('--dark-white-rgb', '0, 0, 0')
      root.style.setProperty('--black', '#dbdbdb')
      root.style.setProperty('--black-rgb', '219, 219, 219')
      root.style.setProperty('--light-black', '#e8e8e8')
      root.style.setProperty('--grey', '#999999')
      root.style.setProperty('--grey-rgb', '153, 153, 153')
      root.style.setProperty('--light-theme', '#3f5f46')

    }else{
      root.style.setProperty('--black', '#292929')
      root.style.setProperty('--black-rgb', '41, 41 ,41')
      root.style.setProperty('--white', '#FFFFFF')
      root.style.setProperty('--white-rgb', '255, 255, 255')
      root.style.setProperty('--dark-white', '#e8e8e8')
      root.style.setProperty('--dark-white-rgb', '232, 232, 232')
      root.style.setProperty('--light-black', '#000')
      root.style.setProperty('--grey', '#808080')
      root.style.setProperty('--grey-rgb', '128, 128, 128')
      root.style.setProperty('--light-theme', '#93d3a2')
    }
    localStorage.setItem('browserData', JSON.stringify(browserData)) 
  }, [browserData])
  
  root.lang = browserData && browserData.language
  root.dir = browserData && browserData.language == 'ar' ? 'rtl' : 'ltr'

  ////////////////////////////////
  //
  const defaultTitle = browserData && browserData.dictionary['Ch7al Machya'] || 'Ch7al Machya'
  const defaultDescription = browserData && browserData.dictionary['description'] || 'Get the best deals on used and new cars and all other vehicles at Ch7al Machya.'
  const [title, setTitle]=useState(defaultTitle)
  const [description, setDescription]=useState(defaultDescription)
  
  const defaultBrowserContextValue= {
    browserData,
    setBrowserData,
    toggleLanguage,
    globalMessage,
    setGlobalMessage,
    searchCategories, 
    setSearchCategories,
    transformedParameters,
    popupVisibility,
    togglePopup,
    openPopup,
    closePopup,
    openReportModal,
    Left, Right, E, S,
    setTitle, setDescription
  }
  
  const [conversations, setConversations] = useState(null)
  const [conversationsLoading, setConversationsLoading] = useState(false)
 
  const defaultUserContextValue={
    userData : userData,
    setUserData : setUserData,
    conversations:conversations,
    setConversations:setConversations,
    conversationsLoading:conversationsLoading,
    setConversationsLoading:setConversationsLoading
  }
  useEffect(()=>{
    document.getElementById('page-title').innerHTML = title
    document.getElementById('page-description').setAttribute('content', description)
  }, [title, description])

  
  return (
    <>
      <BrowserContext.Provider value={defaultBrowserContextValue}>
        <UserContext.Provider value={defaultUserContextValue}>
            {loading ? <SplashScreen/> :  <TranslatedContent language={browserData.language} />}
            { userData.id && <User /> }
        </UserContext.Provider>
      </BrowserContext.Provider> 
    </>
  );
}

export default App;
