import React from 'react'

export const Loader = ({diam, className}) => {
    let spinnerStyle = {
        height : diam + 'px',
        width : diam + 'px',
        display : 'block',
    }
  return (
    <div  style={spinnerStyle} className={ "spinner-border " + className} role="status">
      <span className="sr-only">Loading...</span>
    </div>
  )
}
