import React, { useContext, useEffect, useState } from 'react'
import { forwardRef, useRef, useImperativeHandle } from 'react'
import styled from 'styled-components'
import { createPortal } from 'react-dom'
import axios from 'axios'
import { UserContext } from '../store/UserContext'
import { Loader } from './Loader'
import AutoResizeTextarea from '../Components/UI/AutoResizeTextarea'
import { Message } from './Message'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import { TranslationContext } from '../store/TranslationContext'

const StyledComponent = styled.dialog`
max-width: 800px;
width: -webkit-fill-available;
background-color: transparent;
border: none;
outline: none;
&:-internal-dialog-in-top-layer::backdrop {
    background: rgba(0, 0, 0, 0.9);
}
`

const ReportModal = forwardRef((props, ref) =>{
    const [reportLoading, setReportLoaidng] = useState(false)
    const [errorReporting, setErrorReporting] = useState(false)
    const [reportData, setReportData] = useState(null)   
    const [responseData, setResponseData] = useState(null) 

    const ChildRef = useRef()
    const sharedMethods = () =>({ // to execute openPopUp user sharedMethods().openPopUp()
        openPopUp: openPopUp,
        setData: setdata
    })

    useImperativeHandle(ref, sharedMethods);// use methods and functions of child coponent of thier parent

    const openPopUp = ()=>{
        ChildRef.current.showModal()
    }
    const setdata = (data)=>{
        setReportData(data)
    }
        
    const closeDialogHandler = ()=>{
        ChildRef.current.close()
    }
    
    const userData = useContext(UserContext).userData

    const config = {
        headers : {
          "Content-Type" : "multipart/form-data",
          "Authorization" : `Bearer ${userData.token}`
        }
    }

    const report = ()=>{
        setReportLoaidng(true)
        axios.post(
            '/api/report/',
            {...reportData, note : note},
            config
        ).then((response)=>{
            reportSent.current=true
            setResponseData(response.data)
            setReportLoaidng(false)
            setNote('')
        }).catch((error)=>{ 
            reportSent.current=true
            setErrorReporting(error)
            setReportLoaidng(false)
        })        
    }
    const [note, setNote] = useState('')
    const reportSent = useRef(false)
    const exitClickHandler =(e)=>{
        e.preventDefault()
        reportSent.current=false
        setResponseData(null)
        setErrorReporting(null)
        setNote('')
        closeDialogHandler()
    }
    const navigate= useNavigate()
    const location = useLocation()
    const {translate} = useContext(TranslationContext)
    let JSX =<div className='m-auto'>
        <StyledComponent ref={ChildRef} onClose={closeDialogHandler} >
            <div className='bg-color-white p-3 border rounded'>
                {responseData && <Message variant='success' removeAlert={()=>setResponseData(null)}> {responseData.detail} </Message>}
                {errorReporting && <Message variant='danger' removeAlert={()=>setErrorReporting(null)}> {errorReporting.response.data.detail || errorReporting.message} </Message>}
                {!userData.id && <h5 className='mb-3 d-flex'>{translate('If you want to report this')} {reportData && translate(reportData.type)} { translate('you have to login. Click here') }</h5>}
                { !reportSent.current && userData.id && <h4 className='mb-3'>{translate('Are you sure you want to report this')} {reportData && translate(reportData.type)} { translate('?') }</h4>}
                { userData.id &&  <p className='lh-lg'>{translate('We will review the')} {reportData && translate(reportData.type)} { translate('you reported, and we will notify you when it is done, if we find that the user has broken our guilines he will be submitted to paneties and can even go to close his account.') }<br/>
                    {translate('Thank you for making Ch7al machya a safer place.')}
                </p>}
                { !reportSent.current && userData.id &&
                    <div>
                        <h6 className='mt-3 mb-2'>{translate('Add note')}</h6>
                        <AutoResizeTextarea maxLength={255} className='form-control' value={note} onChange={(e)=>{setNote(e.target.value)}} rows={3} />
                    </div>
                }
                <div className='mt-4'>
                    <form method="dialog" className='d-flex gap-4 flex-row-reverse w-100'>
                        { !reportSent.current && userData.id && <button disabled={reportLoading} className="btn btn-danger px-3 d-flex gap-3" onClick={report}>{translate('Report')} { reportLoading && <Loader diam={23} className='color-white'/>}</button>}
                        { !reportSent.current && userData.id &&<button className="btn btn-outline-dark px-3">{translate('Cancel')}</button>}
                        { (reportSent.current || !userData.id) &&
                            <div className='d-flex justify-content-between w-100'>
                                { (reportSent.current || !userData.id) && <button className="btn btn-outline-success px-4" onClick={exitClickHandler}>{ translate('Exit') }</button>}
                                {!userData.id && <button onClick={()=>{closeDialogHandler(); navigate('/login/?redirect=' + location.pathname)}} className='btn btn-success'>{ translate('Login') }</button> }
                            </div>
                        }

                        
                    </form>
                </div>
            </div>
        </StyledComponent>
    </div>
    return createPortal(JSX, document.getElementById('root'))
})

export default ReportModal