import React, {Suspense, lazy, useContext }from 'react';
import Header from './Components/Header';
import { BrowserRouter as Router, Route, Routes} from 'react-router-dom'
import {UserContext} from './store/UserContext'
import { Message } from './Components/Message';
import { BrowserContext } from './store/BrowserContext';
import Redirect from './Components/Redirect';
import Home from './Screens/Home';
import ScrollToTop from './Components/ScrollToTop';
import ReportModal from './Components/ReportModal';
import SuspenseComponent from './Components/SuspenseComponent';

const EditArticleScreen = <SuspenseComponent Component={lazy(()=>import('./Screens/dashboard/EditArticleScreen'))} />
const LoginScreen = <SuspenseComponent Component={lazy(()=>import('./Screens/LoginScreen'))} />
const RegisterScreen = <SuspenseComponent Component={lazy(()=>import('./Screens/RegisterScreen'))} />
const SearchScreen = <SuspenseComponent Component={lazy(()=>import('./Screens/Search'))} />
const ForgotPasswordScreen = <SuspenseComponent Component={lazy(()=>import('./Screens/ForgotPassword'))} />
const ResetPassword = <SuspenseComponent Component={lazy(()=>import('./Screens/ResetPassword'))} />
const ChatBox = <SuspenseComponent Component={lazy(()=>import('./Screens/Messages/ChatBox'))} />
const Conversations = <SuspenseComponent fetchConversations={false} Component={lazy(()=>import('./Screens/Messages/Conversations'))} />
const ManageEmail = <SuspenseComponent Component={lazy(()=>import('./Screens/settings/ManageEmail'))} />
const ChangePassword = <SuspenseComponent Component={lazy(()=>import('./Screens/settings/ChangePassword'))} />
const DeleteAccount = <SuspenseComponent Component={lazy(()=>import('./Screens/settings/DeleteAccount'))} />
const YourArticles = <SuspenseComponent Component={lazy(()=>import('./Screens/dashboard/YourArticles'))} />
const SavedArticles = <SuspenseComponent Component={lazy(()=>import('./Screens/dashboard/SavedArticles'))} />
const ContactUs = <SuspenseComponent Component={lazy(()=>import('./Screens/ContactUs'))} />
const ArticleScreen = <SuspenseComponent Component={lazy(()=>import('./Screens/user/ArticleScreen'))} />
const ProfilePage = <SuspenseComponent Component={lazy(()=>import('./Screens/user/ProfilePage'))} />
const ProfileScreen = <SuspenseComponent Component={lazy(()=>import('./Screens/settings/ProfileScreen'))} />

function Content() {
  const {userData} = useContext(UserContext)
  const userIsAuthenticated = Boolean(userData.id)
  const {globalMessage, setGlobalMessage, showHeader, openReportModal} = useContext(BrowserContext)
  return (
    <Router>
      <div id='Content' className='order-1'>
        <Header  id='Header'/>
          <ScrollToTop />
          <main id='main'>
            { globalMessage && <Message variant={globalMessage.variant} removeAlert={()=>setGlobalMessage(null)} className={globalMessage.className} >{ globalMessage.text }</Message>}
            <Routes>
              <Route path='/' exact Component={Home}/>
              <Route path='/login/' element={userIsAuthenticated ? <Redirect redirect='/' /> : LoginScreen} />
              <Route path='/register/' element={userIsAuthenticated ? <Redirect redirect='/' /> : RegisterScreen} />
              <Route path='/search/' element={SearchScreen} />
              <Route path='/forgot-password/' element={userIsAuthenticated ? <Redirect redirect='/' /> : ForgotPasswordScreen} />
              <Route path='/reset-password/:email/:uidb64/:token/' element={ResetPassword} />
              <Route path='/messages/:friendId/' element={userIsAuthenticated ? ChatBox : <Redirect redirect='/' />} />
              <Route path='/messages/' element={Conversations} />
              <Route path='/settings/profile/' element={userIsAuthenticated ? ProfileScreen : <Redirect redirect='/' />} />
              <Route path='/settings/manage-email/' element={userIsAuthenticated ? ManageEmail : <Redirect redirect='/' />} />
              <Route path='/settings/change-password/' element={userIsAuthenticated ? ChangePassword : <Redirect redirect='/' />} />
              <Route path='/settings/delete-account/' element={userIsAuthenticated ? DeleteAccount : <Redirect redirect='/' />} />
              <Route path='/dashboard/create-article/' element={userIsAuthenticated ? EditArticleScreen : <Redirect redirect='/' />} />
              <Route path='/dashboard/your-articles/:articleId/' element={userIsAuthenticated ? EditArticleScreen : <Redirect redirect='/' />} />
              <Route path='/dashboard/your-articles/' element={userIsAuthenticated ? YourArticles : <Redirect redirect='/' />} />
              <Route path='/dashboard/saved-articles/' element={userIsAuthenticated ? SavedArticles : <Redirect redirect='/' />} />
              <Route path='/contact-us/' element={ContactUs}/>
              <Route path='/redirect/' element={<Redirect />}/>
              <Route path='/:username/:articleSlug/' element={ArticleScreen} />
              <Route path='/:username/' element={ProfilePage} />
            </Routes>
            <ReportModal ref={ openReportModal } />
          </main>
      </div>
    </Router>
  );
}

export default Content;
