import React, { useContext } from 'react'
import { BrowserContext } from '../../store/BrowserContext'

const ConversationsNotifications = ({unseenConversationsCount, unacknoledgedNotifications}) => {
  const {popupVisibility, togglePopup} = useContext(BrowserContext)
  const {notificationsShown, conversationsShown} = popupVisibility
  return (
    <>
        <button id='conversations-button' className={`btn position-relative mb-1 ${conversationsShown ? 'bg-color-theme text-white' : 'color-theme bg-color-white'}`} onClick={()=>{ togglePopup('conversationsShown')}}><i className="fa-brands fa-facebook-messenger fa-xl"></i><span hidden={unseenConversationsCount == null} className='unseen-conversations-count rounded-4 btn btn-sm px-1 py-0 btn-danger'>{unseenConversationsCount }</span></button>
        <button id='notifications-button' className={`btn position-relative mb-1 ${notificationsShown ? 'bg-color-theme text-white' : 'color-theme bg-color-white'}`} onClick={()=>{ togglePopup('notificationsShown')}}><i className="fa-solid fa-bell fa-xl">{ unacknoledgedNotifications.length > 0 && <span className='dot'></span>}</i></button>
    </>
    )
}

export default ConversationsNotifications